header {
    width: 100%;
    min-height: 816px;
    background: #000;
    
    .media({
        height: auto;
        position: relative;
        overflow-x: hidden;
    }, 576px);

    nav {
        width: 100%;
        min-height: 70px;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;


        .media({
        width: 100%;
        margin: auto;
        }, 1200px);

        .navbar-logo {
            width: 80px;
            height: 27px;
        }

        ul {
            width: 441px;
            display: flex;
            justify-content: space-between;
            .media({
                flex-direction: column;
                justify-content: start;
                position: absolute;
                width: 100%;
                height: calc(100%);
                right: -100%;
                background: #000;
                top: 0;
                padding-top: 70px;
                transition: 1000ms;
                z-index: 1;
            }, 576px);

            li{
                .media({
                    text-align: center;
                },576px);
                a {
                    color: #b0b0b0;
                    font-weight: 600;
                    font-size: 14px;
                    font-family: @OpenSans;
    
                    .media({
                    text-align: center;
                     font-size: 40px;
                     font-weight: 600;
                     background: transparent;
                    }, 576px);
                }
            }
            .x-dn{
                display: none;
                .media({
                    display: block;
                    position: absolute;
                    top: 10px;
                    right: 30px;
                    color: whitesmoke;
                    font-size: 40px;
                    font-weight: 100;
                    font-family: @OpenSans;
                },576px);
            }

    .media({
        a:hover {
            background: #671eaa;
            padding: 10px 20px;
        }
    }, 576px);

        }

        .img-none {
            display: none;

            .media({
                display: block;
                width: 40px;
                height: 40px;
                border-radius: 3px;
            }, 576px);
        }

    }

.media({
    .toggle-nav {
        right: 0;
    }
}

, 576px);



